<template>
  <div>
    <div class="album py-5 bg-light">
      <Spinner v-if="isLoading" />
      <div v-else class="container">
        <!-- UserProfileCard -->
        <UserProfileCard
          :initialProfile="profile"
          :initialIsFollowed="isFollowed"
          @after-follow="afterFollow"
          @after-unfollow="afterUnfollow"
        />
        <div class="row">
          <div class="col-md-4">
            <!-- UserFollowingsCard -->
            <UserFollowingsCard :Followings="profile.Followings" />
            <br />
            <!-- UserFollwersCard -->
            <UserFollowersCard :Followers="profile.Followers" />
          </div>
          <div class="col-md-8">
            <!-- UserCommentsCard -->
            <UserCommentsCard :Comments="profile.Comments" />
            <br />
            <!-- UserFavoritedRestaurantsCard -->
            <UserFavoritedRestaurantsCard :FavoritedRestaurants="profile.FavoritedRestaurants" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserProfileCard from "./../components/UserProfileCard";
import UserFollowingsCard from "./../components/UserFollowingsCard";
import UserFollowersCard from "./../components/UserFollowersCard";
import UserCommentsCard from "./../components/UserCommentsCard";
import UserFavoritedRestaurantsCard from "./../components/UserFavoritedRestaurantsCard";
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";
import { mapState } from "vuex";
import Spinner from "./../components/Spinner";

export default {
  name: "userProfile",
  components: {
    UserProfileCard,
    UserFollowingsCard,
    UserFollowersCard,
    UserCommentsCard,
    UserFavoritedRestaurantsCard,
    Spinner
  },
  computed: {
    ...mapState(["currentUser"])
  },
  data() {
    return {
      profile: {
        id: -1,
        name: "",
        email: "",
        Comments: [],
        FavoritedRestaurants: [],
        Followings: [],
        Followers: []
      },
      isFollowed: false,
      isLoading: true
    };
  },
  methods: {
    async fetchUser(userId) {
      try {
        this.isLoading = true;
        const { data } = await usersAPI.get({ userId });

        if (data.status === "error") {
          throw new Error(data.message);
        }

        const { profile, isFollowed } = data;

        profile.Comments = profile.Comments.filter(
          Comment => Comment.Restaurant
        );

        this.profile = profile;
        this.isFollowed = isFollowed;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        console.error(error);
        Toast.fire({
          icon: "error",
          title: "找不到使用者資料，請稍後嘗試"
        });
      }
    },
    afterFollow(playload) {
      this.isFollowed = playload.isFollowed;
    },
    afterUnfollow(playload) {
      this.isFollowed = playload.isFollowed;
    }
  },
  created() {
    const { id } = this.$route.params;
    this.fetchUser(id);
  },
  beforeRouteUpdate(to, from, next) {
    const { id } = to.params;
    this.fetchUser(id);
    next();
  }
};
</script>